.ProductInformation {
    font-size: 14px;
    line-height: 20px;

    &-Wrapper {
        padding: 0;

        @include desktop {
            padding-block: 32px;
        }
    }

    &-ExpandableContentContent {
        line-height: 20px;
        text-align: justify;

        + div {
            margin-block-end: 12px;
        }

        &_isContentExpanded {
            margin-block-end: 16px;
        }
    }

    &-ExpandableContentHeading {
        @include desktop {
            display: none;
        }
    }

    &-Placeholder {
        margin-block-start: 24px;

        @include mobile {
            margin-block-start: 28px;
        }

        span {
            display: block;
            margin-block-end: 12px;

            @include mobile {
                margin-block-end: 14px;
            }
        }
    }

    &-Content {
        @include desktop {
            margin-block-end: 12px;
        }
    }

    &-Description {
        word-break: break-word;

        @include description-tags {
            font-size: 14px;
            line-height: 20px;
        }

        li {
            &::before {
                position: relative;
                margin-inline-start: 10px;
                margin-inline-end: -25px;
                inset-block-start: -1px;
            }
        }

        @include default-list;

        @include mobile {
            line-height: 20px;
        }
    }
}
