.ProductReviewList {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 24px;
    margin-block-start: 24px;

    @include mobile {
        margin-block-start: 28px;
        grid-template-columns: 1fr;
        grid-gap: 14px;
    }

    &-Title {
        text-align: center;
        font-weight: 700;
        font-size: 24px;
        margin-block-start: 36px;

        @include mobile {
            font-size: 28px;
            margin-block-start: 42px;
        }
    }
}
