:root {
    --compare-button-size: 23px;
}

.ProductCompareButton {
    display: inline-block;
    height: var(--compare-button-size);
    width: var(--compare-button-size);

    &-Button.Button {
        background-color: unset;
        border: none;
        height: var(--compare-button-size);
        padding: 0;

        &:hover,
        &:focus {
            background-color: unset;
            height: var(--compare-button-size);
            padding: 0;
        }
    }
}
